body {
  margin: 0;
}

.flex-container {
  height: calc(100% - 150px);
  width: 100%;
  /* margin: 0 auto; */
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.container {
  height: 100vh;
  width: 100vw;
  max-width: 100vw;
  max-height: 100vh;

  align-items: center;
  justify-content: center;
  text-align: center;
}

.div-send-msg {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.btn-down {
  z-index: 2;
  position: fixed;
  bottom: 0px;
  width: 100%;
  height: auto;
}

#result {
  margin: 0;
  height: 75px;
  box-shadow: 0 0 10px 0 #bbb;
  font-size: 14px;
  line-height: 25px;
}

#box {
  color: black;
}
#text1{
  color: black;
}
